import React from "react";
// import {InternalLink} from "../framework/Links";
// import {RouteID} from "../application/Routes";
import LogoConsole from "../assets/logo-console.png";
import "../framework/site.scss";

export default class ConsoleGroupFeatureSection extends React.PureComponent {
  render() {
    return (
        <section className="featured project">
            <img src={LogoConsole} alt="Console Group logo"/>
            <div>
                <p>Console is a leading property management and trust accounting software developer in Australia.</p>
                <p>We were engaged to assist with Console Pay for Gateway development &mdash; the only fully integrated payments solution in the industry.</p>
                {/*<p><InternalLink routeID={RouteID.ROUTE_ID_CONSOLE_GROUP}>Read the story</InternalLink></p>*/}
            </div>
        </section>
    );
  }
}