import React from "react";
import ContactUsSection from "../sections/ContactUsSection";
import ConsoleGroupFeatureSection from "../sections/ConsoleGroupFeatureSection";
import EngineeringBannerSection from "../sections/EngineeringBannerSection";
import EngineeringContentSection from "../sections/EngineeringContentSection";
import "../framework/site.scss";

export default class EngineeringContent extends React.PureComponent {
  render() {
    return (
      <main>
          <EngineeringBannerSection />
          <EngineeringContentSection showHeader={false} />
          <ContactUsSection />
          <ConsoleGroupFeatureSection />
      </main>
    );
  }
}